import { Header } from "./component/Header";
import { Main } from "./component/Main";
import "./styles.css";

export default function App(props) {
  const themes = props.store.themesData.map((theme) => (
    <Header key={theme.id} theme={theme} />
  ));
  return (
    <div className="App">
      {themes}
      <Main
        memberData={props.store.memberData}
        priceData={props.store.priceData}
      />
    </div>
  );
}
