import { useState, useRef, useEffect } from "react";
import { Members } from "./Members";
import { Price } from "./Price";
import style from "./Main.module.css";

export const TIMER_LENGTH = 120;

export const Main = ({ memberData, priceData }) => {
  const [membersQty, setMembersQty] = useState(0);
  const [seconds, setSeconds] = useState(TIMER_LENGTH);
  const [activeMember, setActiveMember] = useState(0);
  const intervalRef = useRef();

  useEffect(() => {
    if (!memberData.length) return;
    setMembersQty(memberData.length);
  }, [memberData]);

  useEffect(() => {
    intervalRef.current = setInterval(() => {
      setSeconds((sec) => sec - 1);
    }, 1000);
    return () => clearInterval(intervalRef.current);
  }, [activeMember]);

  const checkSeconds = () => {
    if (seconds > 1) return;
    setSeconds(TIMER_LENGTH);
    clearInterval(intervalRef.current);
    if (activeMember === membersQty - 1) {
      setActiveMember(0);
      return;
    }
    setActiveMember((prev) => prev + 1);
  };

  checkSeconds();

  const members = memberData.map((member, index) => (
    <Members
      key={member.id}
      member={member}
      timer={seconds}
      activeMember={activeMember}
      isActive={activeMember === index}
    />
  ));

  const prices = priceData.map((price) => (
    <Price key={price.id} item={price} />
  ));

  return (
    <div className={style.main}>
      <div className={style.element}>
        <div className={style.title}>
          <div className={style.step}>Ход</div>
          <div style={{ marginTop: "10px" }}>Параметры требования</div>
        </div>
        <div className={style.parametrs}>
          <p className={style.parametrСomplex}>
            Наличие комплекса мероприятий, повышающих стандарты качества
            изготовления
          </p>
          <p>Срок изготовления лота, дней</p>
          <p>Гарантийные обязательства, мес</p>
          <p>Условия отплаты</p>
          <p className={style.parametrPrice}>
            Стоимость изготовления лота, руб. (без НДС)
          </p>
          <p>Действия</p>
        </div>
      </div>
      <div className={style.components}>
        <div className={style.component}>{members}</div>
        <div className={style.component}>{prices}</div>
      </div>
    </div>
  );
};
