import { Timer } from "./Timer";
import { TIMER_LENGTH } from "./Main";

export const Members = ({ member, timer, isActive }) => {
  return (
    <div className="members">
      <div>
        <div>
          <Timer timer={isActive ? timer : TIMER_LENGTH} />
          <div style={{ textTransform: "uppercase" }}>{member.number}</div>
          <div style={{ textTransform: "uppercase" }}>{member.member}</div>
        </div>
        <div>
          <p>{"."}</p>
          <p className="memberTime" style={{ marginTop: "25px" }}>
            {member.productionTime}
          </p>
          <p className="memberPeriod">{member.guaranteePeriod}</p>
          <p className="memberPayment">{member.conditionsPayment}</p>
        </div>
      </div>
    </div>
  );
};
